.check-out-right-side{ 
    width: 100%;
    padding: 0rem 4rem;
    box-sizing: border-box;
    margin-top: 7rem;
}

.place-order-button{ 
    padding: 1rem 1rem;
    background-color: black;
    color: white;
    display: flex;
    justify-content: flex-end;
    align-items: center;
    border-radius: 5px;
    width: 100%;
    cursor: pointer;
}

.place-order-button-inner{ 
    width: 60%;
    display: flex;
    justify-content: space-between;
    align-items: center;
}
.tax-tip-local{ 
    margin: 2rem 0rem; 
    width: 100%;

}

.tax, .tip, .local{ 
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0.3rem 0rem;
}
.worker-tip-amnt{ 
    margin: 1rem 0rem;
    display: flex;
    justify-content: space-between;
    align-items: center;
}
.worker{ 
    font-size: 14px;
}

.worker-tip-amnt-btn{ 
    display: flex; 
    justify-content: center;
    align-items: center;
    list-style: none;
    gap: 0.5rem;
}
.worker-tip-amnt-btn li{ 
    padding: 0.5rem 1.5rem;
    background-color: red;
    border-radius: 5px;
    cursor: pointer;
    background-color: var(--worker-tip-amnt-btn);
}

.all-total{ 
    font-size: 20px;
    margin: 1rem 0rem;
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-top: 4rem;
}

.bolder{ 
    font-weight: bolder;
}

.pop-u-checkout{ 
    width: 100%;
    height: fit-content;
    background-color: var(--check-out-page-left); 
    padding: 1rem 1rem;
    border: 1px solid black;
    border-radius: 5px;
}
.pop-up-buttons{ 
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 3rem;
    margin: 2rem 0rem;
}
.pop-up-buttons button{ 
    padding: 1rem 1rem;
    background-color: black;
    color: white;
    border: none;
    /* display: flex;
    justify-content: flex-end;
    align-items: center; */
    border-radius: 5px;
    width: 100%;
    cursor: pointer;
}
.pop-up-text{ 
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    gap: 2rem;
    margin: 2rem 0rem;
}








/* media queries */
@media screen and (max-width: 768px){ 
    .pop-up-buttons{ 
        display: flex;
        flex-direction: column;
        gap: 1rem;
    }
}